/**
 * Store to manage global items, search items, and API key settings.
 */
export const Store = {
  globalItems: {
    keyRegion: "",
  },
  interface: {
    monitorMode: false,
  },
  itemsSearch: [],
  settingsApiKey: {
    redirectUrl: "https://eddybel.github.io/URL-Callback/",
    code: "",
    accessToken: "",
    clientId: "",
    clientSecret: "",
    regionURL: "",
  },

  apikeys: {
    redirectUrl: "https://eddybel.github.io/URL-Callback/",
    code: "",
    accessToken: "",
    clientId: "",
    clientSecret: "",
    regionURL: "",
  },
};

/**
 * ============================================
 * Global Items Management
 * ============================================
 */

/**
 * Sets global items and saves them to localStorage.
 * @param {Object} items - The global items to set.
 */
export function setGlobalItems(items) {
  Store.globalItems = items;
  saveToLocalStorage("globalItems", Store.globalItems);
}

/**
 * Retrieves the global items from the store.
 * @returns {Object} - The global items.
 */
export function getGlobalItems() {
  return Store.globalItems;
}

/**
 * Loads global items from localStorage into the store.
 * @returns {Object} - The loaded global items.
 */
export function loadGlobalItemsFromLocalStorage() {
  Store.globalItems = loadFromLocalStorage("globalItems") || Store.globalItems;
  return Store.globalItems;
}

/**
 * ============================================
 * Search Items Management
 * ============================================
 */

/**
 * Sets search items and saves them to localStorage.
 * @param {Array} items - The search items to set.
 */
export function setSearchItems(items) {
  Store.itemsSearch = items;
  saveToLocalStorage("itemsSearch", Store.itemsSearch);
}

/**
 * Retrieves the search items from the store.
 * @returns {Array} - The search items.
 */
export function getSearchItems() {
  return Store.itemsSearch;
}

/**
 * Loads search items from localStorage into the store.
 * @returns {Array} - The loaded search items.
 */
export function loadSearchItemsFromLocalStorage() {
  Store.itemsSearch = loadFromLocalStorage("itemsSearch") || Store.itemsSearch;
  return Store.itemsSearch;
}

/**
 * ============================================
 * API Key Settings Management
 * ============================================
 */

/**
 * Sets API key settings and saves them to localStorage.
 * @param {Object} settingsApiKey - The API key settings to set.
 */
export function setApiKeySettings(settingsApiKey) {
  Store.settingsApiKey = settingsApiKey;
  saveToLocalStorage("settingsApiKey", settingsApiKey);
}

/**
 * Loads API key settings from localStorage into the store.
 * @returns {Object} - The loaded API key settings.
 */
export function loadApiKeySettingsFromLocalStorage() {
  const store = loadFromLocalStorage("settingsApiKey");

  if (store) {
    Store.settingsApiKey = store;
  }
  return store;
}

/**
 * Sets the region URL for the API key settings and saves it to localStorage.
 * @param {string} regionUrl - The region URL to set.
 */
export function setRegionUrl(regionUrl) {
  Store.settingsApiKey.regionURL = regionUrl;
  saveToLocalStorage("settingsApiKey", Store.settingsApiKey);
}

export function setApiKey(data) {
  Store.apikeys = data;
  saveToLocalStorage("apikeys", Store.apikeys);
}

export function loadApiKey() {
  const store = loadFromLocalStorage("apikeys");
  if (store) {
    Store.apikeys = store;
  }
  return store || Store.apikeys;
}

/**
 * ============================================
 * LocalStorage Utility Functions
 * ============================================
 */

/**
 * Saves an item to localStorage.
 * @param {string} key - The key under which to save the item.
 * @param {any} value - The value to save.
 */
function saveToLocalStorage(key, value) {
  localStorage.setItem(key, JSON.stringify(value));
}

/**
 * Loads an item from localStorage.
 * @param {string} key - The key under which the item is saved.
 * @returns {any} - The loaded value or null if not found.
 */
function loadFromLocalStorage(key) {
  const item = localStorage.getItem(key);
  return item ? JSON.parse(item) : null;
}
