import { REGIONS } from "../configs";
import { loadGlobalItemsFromLocalStorage, setGlobalItems, setRegionUrl, Store } from "../store";
import { $ } from "../utils";

const $Select = $("#region-select");
const $SelectKeys = $("#region-select__keys");
const keysRegions = Object.keys(REGIONS);
const defaultRegion = loadGlobalItemsFromLocalStorage().keyRegion;
$Select.defaultvalue = defaultRegion;

keysRegions.forEach((key) => {
  const option = document.createElement("option");
  option.value = REGIONS[key].id;
  option.textContent = key;
  $Select.appendChild(option);
});
keysRegions.forEach((key) => {
  const option = document.createElement("option");
  option.value = REGIONS[key].id;
  option.textContent = key;
  $SelectKeys.appendChild(option);
});

$Select.addEventListener("change", (event) => {
  setGlobalItems({ ...Store.globalItems, keyRegion: event.target.value });
});

const regionURL = $SelectKeys.value;
let urlAuthByRegion = "";
Object.keys(REGIONS).forEach((key) => {
  if (REGIONS[key].id === regionURL) {
    urlAuthByRegion = REGIONS[key].uriAuth;
  }
});
setRegionUrl(urlAuthByRegion);

$SelectKeys.addEventListener("change", (event) => {
  const regionURL = event.target.value;
  let urlAuthByRegion = "";
  Object.keys(REGIONS).forEach((key) => {
    if (REGIONS[key].id === regionURL) {
      urlAuthByRegion = REGIONS[key].uriAuth;
    }
  });
  setRegionUrl(urlAuthByRegion);
});
