import { Store } from "../store";
import { $, formatAmount, splitArrayIntoChunks } from "../utils";

// DOM Elements
const $AppMain = $("#app__actions");
const $AppFilter = $("#app__filter");
const $LinkFilters = $("#filters-link-button");
const $LinkActions = $("#actions-link-button");
const $ItemsList = $("#items__list");
const $SearchInput = $("#search-input-filter");

/**
 * This function renders an item in the list.
 * @param {*} item
 * @returns
 */
function renderItems(item) {
  if (!item) return;

  const $Item = createItemElement(
    item.thumbnail,
    item.price,
    item?.original_price || item.price || "",
    item?.available_quantity || item?.initial_quantity || "",
    item.title,
    item.id,
    item?.permalink || ""
  );

  $ItemsList.appendChild($Item);
}

$LinkFilters.addEventListener("click", async () => {
  // Oculta el contenido de la aplicacion principal
  $AppMain.classList.add("hidden");
  $AppFilter.classList.remove("hidden");
  $LinkFilters.classList.add("active");
  $LinkActions.classList.remove("active");

  // Carga los items de la API de MercadoLibre
  const items = Store.itemsSearch;
  const itemsChunks = splitArrayIntoChunks(items, 20);

  // Renderiza los botones de paginacin
  const $PaginationButtons = $("#pagination-buttons");
  $PaginationButtons.innerHTML = "";
  itemsChunks?.forEach((_, index) => {
    const $buttonPagination = document.createElement("button");
    $buttonPagination.classList.add("button__list");
    $buttonPagination.innerHTML = index + 1;
    $PaginationButtons.appendChild($buttonPagination);
    $buttonPagination.addEventListener("click", async () => {
      // Carga los items de la API de MercadoLibre
      const $ItemsList = $("#items__list");
      $ItemsList.innerHTML = "";
      itemsChunks[index].forEach((item) => {
        renderItems(item);
      });
    });
  });

  const $ItemsList = $("#items__list");
  $ItemsList.innerHTML = "";
  itemsChunks[0]?.forEach((item) => {
    renderItems(item);
  });
});

$LinkActions.addEventListener("click", async () => {
  // Oculta el contenido de la aplicacion principal
  $AppMain.classList.remove("hidden");
  $AppFilter.classList.add("hidden");
  $LinkFilters.classList.remove("active");
  $LinkActions.classList.add("active");
});

function createItemElement(img, price, originalPrice, units, name, id, link) {
  // Crear los elementos principales
  const li = document.createElement("li");
  const itemCard = document.createElement("div");
  itemCard.className = "item__card";

  // Crear la seccin de informacin del item
  const infoContainer = document.createElement("div");
  infoContainer.className = "item__card__info__container";

  const imgElement = document.createElement("img");
  imgElement.src = img;
  imgElement.alt = "item";
  imgElement.className = "item__card__image";

  const textContainer = document.createElement("div");

  const title = document.createElement("h1");
  title.className = "item__card__title";
  title.textContent = name;

  const dataContainer = document.createElement("div");
  dataContainer.className = "item__card__data";

  const priceContainer = document.createElement("div");
  priceContainer.className = "item__card__data__price";

  const originalPriceElem = document.createElement("p");
  originalPriceElem.className = "item__card__data__original__price";
  originalPriceElem.textContent = formatAmount(originalPrice);

  const priceElem = document.createElement("p");
  priceElem.className = "item__card__data__discount";
  priceElem.textContent = formatAmount(price);

  priceContainer.appendChild(originalPriceElem);
  priceContainer.appendChild(priceElem);

  const unitsContainer = document.createElement("div");

  const idElem = document.createElement("p");
  idElem.className = "item__card__data__units";
  idElem.textContent = id;

  const unitsElem = document.createElement("p");
  unitsElem.className = "item__card__data__units";
  unitsElem.textContent = `Uds ${units}`;

  unitsContainer.appendChild(idElem);
  unitsContainer.appendChild(unitsElem);

  dataContainer.appendChild(priceContainer);
  dataContainer.appendChild(unitsContainer);

  textContainer.appendChild(title);
  textContainer.appendChild(dataContainer);

  // Construir la seccin de acciones del item
  const actionsContainer = document.createElement("div");
  actionsContainer.className = "item__card__actions";

  const addButton = document.createElement("button");
  addButton.className = "button add";
  addButton.textContent = "Agregar";
  addButton.addEventListener("click", async () => {
    try {
      await Excel.run(async (context) => {
        const table = context.workbook.tables.getItem("MercadoLibre");

        // Cargar la informacin de las columnas
        table.columns.load("items/name,index");
        await context.sync();

        // Verificar la columna ID y obtener su ndice
        const idColumn = table.columns.items.find((col) => col.name === "ID");
        if (!idColumn) {
          throw new Error("La columna 'ID' no existe en la tabla.");
        }
        const idIndex = idColumn.index;

        // Preparar los datos para agregar la fila
        const rowData = Array(table.columns.items.length).fill(null); // Rellenar con valores nulos
        rowData[idIndex] = id; // Solo agregar el ID en la columna correspondiente

        // Agregar una nueva fila con los datos
        table.rows.add(null, [rowData]);
        await context.sync();
      });
    } catch (error) {
      console.error("Error adding item:", error);
    }
  });

  const webButton = document.createElement("a");
  webButton.className = "button web";
  webButton.textContent = "Ver";
  webButton.href = link;
  webButton.target = "_blank";

  actionsContainer.appendChild(addButton);
  actionsContainer.appendChild(webButton);

  // Ensamblar todo
  infoContainer.appendChild(imgElement);
  infoContainer.appendChild(textContainer);

  itemCard.appendChild(infoContainer);
  itemCard.appendChild(actionsContainer);

  li.appendChild(itemCard);

  return li;
}

$SearchInput.addEventListener("input", (event) => {
  const searchTerm = event.target.value.trim().toLowerCase();
  const newListItems = [...Store.itemsSearch];

  // Filtra los elementos segn el trmino de bsqueda
  let items =
    searchTerm == "" || searchTerm == null || searchTerm == " " || searchTerm == undefined
      ? newListItems // Utiliza la copia de los datos originales si la bsqueda est vaca
      : removeDuplicates(Store.itemsSearch.filter((item) => item.title.toLowerCase().includes(searchTerm)));

  // Divide los elementos en grupos de 20
  const itemsChunks = splitArrayIntoChunks(items, 20);

  // Renderiza la primera pgina de resultados
  renderPage(0, itemsChunks);

  // Configura los botones de paginacin
  setupPagination(itemsChunks);
});

// Funcin para eliminar elementos duplicados de una lista
function removeDuplicates(items) {
  const seen = new Set();
  return items.filter((item) => {
    const duplicate = seen.has(item.id);
    seen.add(item.id);
    return !duplicate;
  });
}

// Funcin para renderizar una pgina especfica de resultados
function renderPage(pageIndex, itemsChunks) {
  const $ItemsList = $("#items__list");
  $ItemsList.innerHTML = ""; // Limpia la lista antes de renderizar

  itemsChunks[pageIndex]?.forEach((item) => {
    renderItems(item); // Renderiza los elementos de la pgina actual
  });
}

// Funcin para configurar los botones de paginacin
function setupPagination(itemsChunks) {
  const $PaginationButtons = $("#pagination-buttons");
  $PaginationButtons.innerHTML = ""; // Limpia los botones antes de renderizar

  itemsChunks.forEach((items, index) => {
    if (items) {
      const $buttonPagination = document.createElement("button");
      $buttonPagination.classList.add("button__list");
      $buttonPagination.textContent = index + 1;
      $PaginationButtons.appendChild($buttonPagination);

      $buttonPagination.addEventListener("click", () => {
        renderPage(index, itemsChunks); // Renderiza la pgina correspondiente al botn
      });
    }
  });
}
