import "./scripts/render.js";
import { fetchItemsByIds, getItemsByIteration, searchItemsByRegion } from "./scripts/api";
import { GetAllIds, renderItemsValuesInTable, renewItemsValuesInTable, splitArrayIntoChunks } from "./scripts/utils";
import { $ } from "./scripts/utils";
import { EXCEL_TABLES } from "./scripts/configs.js";
import { loadApiKey, Store } from "./scripts/store.js";
import { setSearchItems } from "./scripts/db/database.db.js";

/* global console, document, Excel, Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    // Obtener los elementos de la interfaz de usuario
    $("#update-data-in-scraping").onclick = getAllItems;
    $("#search-button").onclick = searchItems;
    $("#toggle-monitor-mode").onclick = toggleMonitorMode;
  }
});

export async function run() {
  try {
    await Excel.run(async (context) => {
      /**
       * Insert your Excel code here
       */
      const range = context.workbook.getSelectedRange();

      // Read the range address
      range.load("address");

      // Update the fill color
      range.format.fill.color = "yellow";

      await context.sync();
      console.log(`The range address was ${range.address}.`);
    });
  } catch (error) {
    console.error(error);
  }
}

export async function getAllItems() {
  try {
    await Excel.run(async (context) => {
      // Obtener los IDs de todos los items en la tabla "MercadoLibre"
      const IDs = await GetAllIds(context, "MercadoLibre");
      const accessToken = loadApiKey()?.accessToken;
      const results = [];

      if (!accessToken) {
        $("#loader-actions").classList.add("activate");
        const response = await getItemsByIteration(IDs);
        renderItemsValuesInTable(context, "MercadoLibre", response);
        $("#loader-actions").classList.remove("activate");
      } else {
        $("#loader-actions").classList.add("activate");
        const IDsChunks = splitArrayIntoChunks(IDs, 20);

        for (const chunk of IDsChunks) {
          const response = await fetchItemsByIds(chunk, accessToken);
          if (response) {
            response.forEach((item) => {
              results.push(item?.code == 200 ? item?.body : null);
            });
          }
        }

        const resultsMap = new Map(results.map((item) => [item.id, item]));
        const reorderedResults = IDs.map((id) => resultsMap.get(id) || null);

        // Ahora renderizas los resultados y remueves la clase del loader
        renderItemsValuesInTable(context, "MercadoLibre", reorderedResults);
        $("#loader-actions").classList.remove("activate");
        console.log("Datos obtenidos");
      }
    });
  } catch (error) {
    console.error("Error retrieving IDs:", error);
    $("#loader-actions").classList.remove("activate");
  }
}

export async function toggleMonitorMode() {
  const indicatorMonitor = $("#monitor-mode-indicator");
  let deleteInterval = () => {};

  if (!Store.interface.monitorMode) {
    Store.interface.monitorMode = true;
    indicatorMonitor.classList.add("active");
    const intervalMinutes = 10;
    const intervalMs = intervalMinutes * 60 * 1000;

    // Iniciar el monitor
    await getAllItems();
    const idInterval = setInterval(async () => {
      await getAllItems();
    }, intervalMs);

    deleteInterval = () => {
      clearInterval(idInterval);
    };
  } else {
    Store.interface.monitorMode = false;
    indicatorMonitor.classList.remove("active");
    deleteInterval();
  }
}

export async function searchItems() {
  try {
    await Excel.run(async (context) => {
      const sheet = EXCEL_TABLES.SEARCH.sheet;
      const table = EXCEL_TABLES.SEARCH.table;
      const loader = $("#loader-search");
      const region = $("#region-select").value;
      const search = $("#search-input").value;
      const pages = $("#pages-input").value || 1;
      const accessToken = loadApiKey()?.accessToken;

      if (region || search) {
        loader.classList.add("activate");
        const results = await searchItemsByRegion(search, region, pages, accessToken);
        if (results) {
          Store.itemsSearch = results;
          renewItemsValuesInTable(context, table, sheet, results);
        }
        console.log("Resultados de la API de Busqueda de MercadoLibre", results);
        loader.classList.remove("activate");
      } else {
        console.log("No se ha seleccionado una region", region, search, pages);
      }
    });
  } catch (error) {
    console.error("Error retrieving IDs:", error);
    $("#loader-search").classList.remove("activate");
  }
}
