import { getAccessToken, openAuthWindow } from "../api";
import { REGIONS } from "../configs";
import {
  loadApiKey,
  loadApiKeySettingsFromLocalStorage,
  setApiKey,
  setApiKeySettings,
  setRegionUrl,
  Store,
} from "../store";
import { $ } from "../utils";

// Seccion de configuracin de llaves
const $KeyClientId = $("#key_client_id");
const $KeySecretId = $("#key_secret_id");
const $KeyCodeId = $("#key_code_id");
const $KeyAccessId = $("#key_access_id");
const $KeyUrlRedirect = $("#key_url_redirect");
const $KeyAccessToken = $("#access-token");
const $SelectKeys = $("#region-select__keys");
const $ButtonAuthApp = $("#authapp-button");
const $ButtonGetAccessToken = $("#get-access-token-button");

const settingsApiKey = loadApiKey();
$KeyClientId.value = settingsApiKey.clientId;
$KeySecretId.value = settingsApiKey.clientSecret;
$KeyCodeId.value = settingsApiKey.code;
$KeyAccessId.value = settingsApiKey.accessToken;
$KeyUrlRedirect.value = settingsApiKey.redirectUrl;

// Carga las opciones de seleccin de regin.
const keysRegions = Object.keys(REGIONS);
keysRegions.forEach((key) => {
  const option = document.createElement("option");
  option.value = REGIONS[key].id;
  option.textContent = key;
  $SelectKeys.appendChild(option);
});

function updateRegionUrl(regionId) {
  const store = loadApiKey();
  const region = Object.values(REGIONS).find((region) => region.id === regionId);
  if (region) setApiKey({ ...store, regionURL: region.uriAuth });
}

const regionURL = $SelectKeys.value;
updateRegionUrl(regionURL);
$SelectKeys.addEventListener("change", (event) => {
  const regionID = event.target.value;
  updateRegionUrl(regionID);
});

// Funcin para actualizar el estado de settingsApiKey
function updateSettingsApiKey(key, value) {
  setApiKey({ ...Store.apikeys, [key]: value });
}

// Eventos para $KeyClientId
$KeyClientId.addEventListener("change", (event) => {
  updateSettingsApiKey("clientId", event.target.value);
});

$KeyClientId.addEventListener("input", (event) => {
  updateSettingsApiKey("clientId", event.target.value);
});

$KeyClientId.addEventListener("paste", (event) => {
  setTimeout(() => {
    updateSettingsApiKey("clientId", event.target.value);
  }, 0);
});

$KeyClientId.addEventListener("copy", (event) => {
  updateSettingsApiKey("clientId", event.target.value);
});

// Eventos para $KeySecretId
$KeySecretId.addEventListener("change", (event) => {
  updateSettingsApiKey("clientSecret", event.target.value);
});

$KeySecretId.addEventListener("input", (event) => {
  updateSettingsApiKey("clientSecret", event.target.value);
});

$KeySecretId.addEventListener("paste", (event) => {
  setTimeout(() => {
    updateSettingsApiKey("clientSecret", event.target.value);
  }, 0);
});

$KeySecretId.addEventListener("copy", (event) => {
  updateSettingsApiKey("clientSecret", event.target.value);
});

// Eventos para $KeyCodeId
$KeyCodeId.addEventListener("change", (event) => {
  updateSettingsApiKey("code", event.target.value);
});

$KeyCodeId.addEventListener("input", (event) => {
  updateSettingsApiKey("code", event.target.value);
});

$KeyCodeId.addEventListener("paste", (event) => {
  setTimeout(() => {
    updateSettingsApiKey("code", event.target.value);
  }, 0);
});

$KeyCodeId.addEventListener("copy", (event) => {
  updateSettingsApiKey("code", event.target.value);
});

// Eventos para $KeyAccessId
$KeyAccessId.addEventListener("change", (event) => {
  updateSettingsApiKey("accessToken", event.target.value);
});

$KeyAccessId.addEventListener("input", (event) => {
  updateSettingsApiKey("accessToken", event.target.value);
});

$KeyAccessId.addEventListener("paste", (event) => {
  setTimeout(() => {
    updateSettingsApiKey("accessToken", event.target.value);
  }, 0);
});

$KeyAccessId.addEventListener("copy", (event) => {
  updateSettingsApiKey("accessToken", event.target.value);
});

// Eventos para iniciar la autenticacin de aplicacin
$ButtonAuthApp.addEventListener("click", async () => {
  const settingsAPI = loadApiKey();
  const clientId = settingsAPI.clientId;
  const redirectUrl = settingsAPI.redirectUrl;
  const regionURL = settingsAPI.regionURL;
  openAuthWindow(regionURL, clientId, redirectUrl);
});

$ButtonGetAccessToken.addEventListener("click", async () => {
  const settingsAPI = loadApiKey();
  const clientId = settingsAPI.clientId;
  const secretKey = settingsAPI.clientSecret;
  const code = settingsAPI.code;
  const redirectUrl = settingsAPI.redirectUrl;
  const codeVerifier = settingsAPI.code;

  const { accessToken, refreshToken } = await getAccessToken(clientId, secretKey, code, redirectUrl, codeVerifier);
  if (accessToken) {
    $KeyAccessToken.value = accessToken;
    $KeySecretId.value = secretKey;
    updateSettingsApiKey("accessToken", accessToken);
    updateSettingsApiKey("clientSecret", secretKey);
  }
});
