import "./DOM/selects.section";
import "./DOM/apiSettings.section.js";
import "./DOM/filtersPage.section.js";
import { REGIONS } from "./configs.js";
import { loadApiKey, loadGlobalItemsFromLocalStorage, setGlobalItems, Store } from "./store.js";
import { $ } from "./utils.js";

const $AlertAccessToken = $("#alert-access-token");
const ApiKeys = loadApiKey();

if (!ApiKeys.accessToken) {
  $AlertAccessToken.classList.add("hidden");
}

window.addEventListener("DOMContentLoaded", async () => {
  const $Select = $("#region-select");

  const defaultRegion = loadGlobalItemsFromLocalStorage().keyRegion;
  $Select.defaultvalue = defaultRegion;
  const keysRegions = Object.keys(REGIONS);
  keysRegions.forEach((key) => {
    const option = document.createElement("option");
    option.value = REGIONS[key].id;
    option.textContent = key;
    $Select.appendChild(option);
  });

  $Select.addEventListener("change", (event) => {
    setGlobalItems({ ...Store.globalItems, keyRegion: event.target.value });
  });
});
