/** Funciones de utilidad para facilitar el manejo de la interfaz de usuario */
export const $ = (el) => document.querySelector(el);
export const $$ = (el) => document.querySelectorAll(el);

/** Function que obtiene todos los IDs de los items en una tabla */
export async function GetAllIds(context, tableName) {
  // Accede a la tabla llamada "MercadoLibre"
  const table = context.workbook.tables.getItem(tableName);
  // Obtn el rango de la columna de IDs (asumiendo que los IDs estn en la primera columna)
  const idColumn = table.columns.getItemAt(0).getDataBodyRange();

  // Carga los valores de la columna
  idColumn.load("values");
  // Sincroniza el contexto para obtener los datos
  await context.sync();

  // Extrae los IDs y gurdalos en una lista
  return idColumn.values.map((row) => row[0]);
}

/**
 * Funcin que se encarga de renderizar los valores de los items en una tabla
 */
export async function renderItemsValuesInTable(context, tableName, items) {
  try {
    const table = context.workbook.tables.getItem(tableName);

    // Cargar la informacin de las columnas de la tabla
    table.columns.load("items/name,index");
    await context.sync(); // Sincronizar para asegurarse de que la informacin de las columnas est disponible

    // Obtener los ndices de las columnas por nombre
    const columnIndices = {};
    table.columns.items.forEach((column) => {
      columnIndices[column.name] = column.index; // Asignar el nombre de la columna a su ndice correspondiente
    });

    // Iterar sobre cada item en la lista
    for (const [rowIndex, item] of items?.entries() || []) {
      const data = {
        ["URL"]: item?.permalink || null,
        ["Nombre"]: item?.title || null,
        ["Precio"]: item?.price || null,
        ["Precio Original"]: item?.original_price || null,
        ["Unidades Vendidas"]: item?.avaliable_quantity || item?.initial_quantity || null,
        ["Imagen Url"]: item?.thumbnail || null,
        ["Estado"]: item?.status || null,
      };

      // Cargar la fila y sus celdas
      const row = table.rows.getItemAt(rowIndex);
      row.load("values");
      await context.sync(); // Sincronizar para asegurarse de que los valores de la fila estn disponibles

      // Iterar sobre cada propiedad en `data` y actualizar solo las celdas correspondientes
      for (const [header, value] of Object.entries(data)) {
        if (value !== null && columnIndices[header] !== undefined) {
          const columnIndex = columnIndices[header];
          const cellValue = row.values[0][columnIndex];

          // Solo actualizar la celda si el valor es diferente
          if (cellValue !== value) {
            row.getRange().getCell(0, columnIndex).values = [[value]];
          }
        }
      }
    }

    // Sincronizar para aplicar los cambios
    await context.sync();
  } catch (error) {
    console.error("Error updating table rows:", error);
  }
}

export async function renewItemsValuesInTable(context, tableName, sheetName, items) {
  try {
    const sheet = context.workbook.worksheets.getItem(sheetName);
    const table = sheet.tables.getItem(tableName);

    // Eliminar todas las filas existentes en la tabla
    await deleteAllRowsInTable(context, tableName);

    // Cargar las columnas de la tabla para obtener sus ndices
    table.columns.load("items/name,index");
    await context.sync(); // Sincronizar para asegurarse de que la informacin de las columnas est disponible

    // Crear un objeto para mapear los nombres de las columnas con sus ndices
    const columnIndices = {};
    table.columns.items.forEach((column) => {
      columnIndices[column.name] = column.index;
    });

    // Crear una matriz para almacenar las nuevas filas
    const newRows = [];

    // Iterar sobre cada item en la lista y construir los datos de las filas
    items?.forEach((item) => {
      // Crear un array para representar una fila completa con valores predeterminados
      const rowData = new Array(table.columns.items.length).fill("");

      // Asignar valores a las celdas correspondientes segn el nombre de la columna
      rowData[columnIndices["ID"]] = item?.id || "";
      rowData[columnIndices["URL"]] = item?.permalink || "";
      rowData[columnIndices["Nombre"]] = item?.title || "";
      rowData[columnIndices["Precio"]] = item?.price || "";
      rowData[columnIndices["Precio Original"]] = item?.original_price || "";
      rowData[columnIndices["Unidades Vendidas"]] = item?.available_quantity || item?.initial_quantity || "";
      rowData[columnIndices["Imagen Url"]] = convertToHttps(item?.thumbnail || "");

      // Agregar la fila completa a la matriz de nuevas filas
      newRows.push(rowData);
    });

    // Agregar las nuevas filas a la tabla
    if (newRows.length > 0) {
      table.rows.add(null, newRows);
    }

    // Reaplicar la frmula en la columna "Preview"
    const previewColumnIndex = columnIndices["Preview"];
    if (previewColumnIndex !== undefined) {
      const rowCount = newRows.length;
      const formulaArray = new Array(rowCount).fill([`=IMAGEN([@[Imagen Url]])`]);

      const formulaRange = table.columns.getItemAt(previewColumnIndex).getDataBodyRange();
      formulaRange.formulas = formulaArray;
    }

    // Sincronizar para aplicar los cambios
    await context.sync();
  } catch (error) {
    console.error("Error updating table rows:", error);
  }
}

export async function deleteAllRowsInTable(context, tableName) {
  try {
    const table = context.workbook.tables.getItem(tableName);
    const rows = table.rows;

    // Eliminar todas las filas de la tabla
    rows.load("count"); // Carga el nmero de filas actuales
    await context.sync();

    // Elimina las filas desde la ltima hacia la primera para evitar problemas de ndice
    for (let i = rows.count - 1; i >= 0; i--) {
      rows.getItemAt(i).delete();
    }

    await context.sync();
    console.log(`Todas las filas de la tabla ${tableName} han sido eliminadas.`);
  } catch (error) {
    console.error("Error updating table rows:", error);
  }
  return;
}

/** Funcion que separa un array en chunks de un tamao determinado */
export function splitArrayIntoChunks(array, maxElementsPerChunk) {
  const result = [];

  for (let i = 0; i < array.length; i += maxElementsPerChunk) {
    result.push(array.slice(i, i + maxElementsPerChunk));
  }

  return result;
}

/** Convierte una URL a HTTPS si no est ya en HTTPS */
export function convertToHttps(url) {
  if (!url || url.length === 0) return "";
  if (url.startsWith("http://")) return url.replace("http://", "https://");
  return url; // Si la URL ya es https o no comienza con http://, la retorna sin cambios
}

/**
 * Valida si un parmetro es un nmero y existe, luego lo formatea como dinero (USD por defecto).
 * @param {any} amount - El valor a validar y formatear.
 * @param {string} [locale="en-US"] - La configuracin regional opcional para formatear el dinero.
 * @param {string} [currency="USD"] - La moneda opcional para formatear el dinero.
 * @returns {string|null} - El valor formateado como dinero o `null` si no es un nmero vlido.
 */
export function formatAmount(amount, locale = "en-US", currency = "USD") {
  if (typeof amount === "number" && !isNaN(amount)) {
    return new Intl.NumberFormat(locale, {
      style: "currency",
      currency: currency,
    }).format(amount);
  }
  return null;
}
